:root {
  --clr-primary-100: hsl(193deg, 38%, 86%);
  --clr-primary-200: hsl(150deg, 100%, 66%);
  --clr-neutral-100: hsl(217deg, 19%, 38%);
  --clr-neutral-200: hsl(217deg, 19%, 24%);
  --clr-neutral-300: hsl(218deg, 23%, 16%);
  --ff-primary: "Manrope", sans-serif;
  --fw-400: 400;
  --fw-800: 800;
  --bg-color: var(--clr-neutral-300);
}

/* Box sizing rules */
*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Remove default margin */
html,
body,
p {
  margin: 0;
  padding: 0;
  font-size: inherit;
}

/* Set core root defaults */
html:focus-within {
  scroll-behavior: smooth;
}

/* Set core body defaults */
body {
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

/* Make images easier to work with */

/* Text Color Utility Classes */
.text-primary-100 {
  color: var(--clr-primary-100);
}

.text-primary-200 {
  color: var(--clr-primary-200);
}

/* Background Color Utility Classes */

.container {
  position: relative;
  background-color: var(--clr-neutral-200);
  text-align: center;
  max-width: 33.75rem;
  margin-left: auto;
  margin-right: auto;
  padding-top: 2.8125rem;
  padding-right: 0.5625rem;
  padding-bottom: 3.75rem;
  padding-left: 0.5625rem;
  border-radius: 1rem;
}
@media (min-width: 50em) {
  .container {
    padding-top: 3.125em;
    padding-right: 3.125em;
    padding-bottom: 4.375em;
    padding-left: 3.125em;
  }
}

body {
  background-color: var(--bg-color);
  font-family: var(--ff-primary);
  font-weight: var(--fw-800);
  display: grid;
  place-content: center;
}

.attribution {
  font-size: 11px;
  text-align: center;
  margin-top: 5rem;
}

.attribution a {
  color: hsl(228deg, 45%, 44%);
}

.advice-title {
  text-transform: uppercase;
  font-size: 1rem;
  letter-spacing: 4px;
  margin-bottom: 2rem;
}

.advice-text {
  position: relative;
  font-size: 1.75rem;
  padding-bottom: 1.875rem;
}
.advice-text p::before {
  content: open-quote;
}
.advice-text p::after {
  content: close-quote;
}
.advice-text::before {
  content: "";
  position: absolute;
  height: 100%;
  width: 100%;
  background-image: url("../images/pattern-divider-mobile.svg");
  background-position: bottom;
  background-repeat: no-repeat;
  left: 0;
}
@media (min-width: 50em) {
  .advice-text {
    padding-bottom: 2.8125rem;
  }
  .advice-text::before {
    background-image: url("../images/pattern-divider-desktop.svg");
  }
}

.button {
  position: absolute;
  bottom: -2rem;
  transform: translateX(-50%);
  background-color: var(--clr-primary-200);
  border: none;
  border-radius: 50%;
  min-height: 4rem;
  min-width: 4rem;
}
.button::before {
  content: "";
  height: 100%;
  width: 100%;
  position: absolute;
  background-image: url("../images/icon-dice.svg");
  background-repeat: no-repeat;
  top: 30%;
  left: 31%;
}
.button:hover {
  cursor: pointer;
}